import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";

export interface Bar {
  name: string;
  address: string;
  WeekOpen: string;
  WeekClose: string;
  WeekendOpen: string;
  WeekendClose: string;
  State: string;
}

export interface BarMenuItem {
  beer: string;
  manf: string;
  price: number;
  likes: number;
}
@Injectable({
  providedIn: 'root'
})
export class BarsService {

  constructor(
    public http: HttpClient
  ) { }
  getBars() {
    return this.http.get<Bar[]>('/api/bar');
  }
  getBar(bar: string) {
    return this.http.get<Bar>('/api/bar/' + bar)
  }
  getBarList() {
    return this.http.get<string[]>('/api/bar-only');
  }
  getTop10Bar(bar: string) {
    return this.http.get<string[]>(`/api/bar-spender-10/${bar}`);
  }
  getMenu(bar: string) {
    return this.http.get<BarMenuItem[]>('/api/menu/' + bar);
  }

  getFrequentCounts() {
    return this.http.get<any[]>('/api/frequents-data')
  }
}
